.nav-toDo{
    background-color: #b4bbff;
}

.btn-add{
    background-color: $white;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    outline: none;
    border: none;
    width: 4rem;
    height: 4rem;
    font-size: 2.5rem;
    font-family: $fuente_nav;
    padding: 0.2rem .8rem;
    &:hover{
        background-color: #8995fd;
        color: $white;
    }

    @include tablet{
        &:after{
            content : "Add";
        }
        width: 8rem;
    }
}

@media (min-width: 992px){
    .navbar-expand-lg .nav-list .navbar-collapse {
        display: none !important;
    }
}

.nav-list{
    justify-content: center !important;

    .to-button{
        order: 3;
    }
    .to-tasks-list{
        order:4;
    }
    .actions{
        order: 2;
    }
    @media (min-width: 765px){
        &{
            justify-content: space-between !important;
        }
    }

}

