html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-size: 1.6rem;
    line-height: 1.8;
    background-color: #dadcfa
}
p {
  
    font-size: 2rem;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
    font-family: $fuente_nav;
    font-weight: $bold;
    font-size: 3.5rem;
}


h1, h2, h3 {

    font-weight: 300;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
    font-family: $fuente_list-txt;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

.text-purple{
    color: #B080FF
}


@media (min-width: 1200px){
    .display-1 {
        font-size: 9rem;
    }
}

.border-bg-w{
    border-radius: 3rem;
}

.h2-font{
    font-family: $fuente_list-txt;
    font-size: 5rem;
}

.text-font{
    font-family: $fuente_list-txt;
}
