.container-white{
    background-color: rgba(255,255,255,89);
    border-radius: 3rem;
    .container{
        .card{
            border-radius: 2rem;
        }
    }
}

.container-list{
    background-color: rgba(226,228,255, .76);
    border-radius: 2rem;
    max-width: 10rem;
    max-height: 9rem;
    p{
        font-family: $fuente_list-txt;
        margin: 0;
    }

}


@media (max-width: 992px){
    .nav-lateral{
        display: none;
    }   
}

.edit-dlt{
    font-size: 2rem;
}

// todo el css de la barra lateral 
.nav-lateral{
    
    font-size: 2.5rem;
    ul{
        margin-top: 9rem;
        margin-left: 9rem;
        list-style: none;
        li{
            margin-bottom: 5rem;
        }
        .active{
            background-color: $white;
            padding: 1rem;
            width: 30rem;
            border-radius: 1.5rem;
        }
    }
    
}   

.cart-ntg{
    background-color: #e2e4ffda
}