
.border-btn{
    border-radius: 1.1rem;
    padding: 1.3rem;
    box-shadow: inset 0px 2px 3px rgba(0, 0, 0, 0.25);
} 

.color-bt{
    background-color: $purple_btn;
}


.btn-login{
    border-radius: .5rem;
    padding: .5rem;
    background-color: $purple;
    width: 100%;
    font-size: 2.5rem;
    font-family: $fuente_list-txt;
    color: #fff;
    font-weight: bold;
    &:hover {
        background-color: lighten($purple, 5);
        cursor: pointer;
    }
    @include desktop{
        width: 30%;
    }

}





