.form-card{
    margin: 0 auto;
    max-width: 90rem;
    background-color: #CCD1FF;
    padding: 3rem;
    border-radius: 2rem;

    .task-form{
        border: 1px solid #B080FF;
        border-radius: 2rem;
        padding: 2rem;
    }
}


.select-list, .form-date{
    width: 45%;
    margin-right: 0;
    outline: none;
    border: none;
    border-radius: .4rem;
    height: 3rem;
    padding: .3rem;
}

.form-title{
    background-color: #CCD1FF !important;
    border: none !important;  
    font-size: 2rem;
    height: 6rem !important;
}
.form-btn{
    max-width: 15rem;

}


//form show 

.form-show , .form-show-list{
    position: fixed;
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    top: 0;
}

// from hide 

.form-hide {
    display: none;
}